import React from "react"
import { Link } from "gatsby"



const NotFoundPage = () => (
<div>
  <h1>Sayfa Bulunamadı</h1>
  <Link 
  className="btn btn-warning" 
  to={'/'}
  style={{display: "flex", justifyContent: "center", textAlign: "center"}}
  >Anasayfaya Dön</Link>
</div>
)

export default NotFoundPage

